/* benefits */
.benefits {
  padding-top: 160px;

  &__title {
    margin-bottom: 50px;
  }

  &__descr {
    margin-bottom: 65px;
    width: 40%;

    @media (max-width: 1024px) {
      width: 100%;
    }
  }

  &__list {
    display: grid;
    column-gap: 30px;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 58px;

    @media (max-width: 1024px) {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 50px;
    }

    @media (max-width: 568px) {
      grid-template-columns: 1fr;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 50px;
  }
}

/* benefits-item */
.benefits-item {
  &__big-text {
    display: block;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.21em;
    text-transform: uppercase;
  }

  &__small-text {
    display: block;
    color: var(--gray-color);
  }
}
