.profit {
  padding-top: 130px;

  &__list {
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      row-gap: 40px;
    }
  }

  &__item {
    &:nth-child(1) {
      grid-column: 1 / 4 span;

      @media (max-width: 1024px) {
        grid-column: 1 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1 span;
        grid-row: 1 / 1 span;
      }
    }

    &:nth-child(2) {
      grid-column: 5 / 4 span;

      @media (max-width: 1024px) {
        grid-column: 4 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1 span;
        grid-row: 2 / 1 span;
      }
    }

    &:nth-child(3) {
      grid-column: 9 / 4 span;

      @media (max-width: 1024px) {
        grid-column: 7 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1 span;
        grid-row: 3 / 1 span;
      }
    }
  }

  &__big-text {
    display: block;
    margin-bottom: 46px;
    font-weight: 600;
    font-size: 46px;
    line-height: 1.21em;

    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
  }

  @media (max-width: 768px) {
    padding-top: 30px;
  }
}
