.section-top-block {
  &__title {
    position: relative;
    grid-column: 1 / 8 span;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 34%;
      width: 27.5%;
      height: 1px;
      background: url("../img/bg-line.svg") right center / cover no-repeat;

      @media (max-width: 1024px) {
        display: none;
      }

      @media (max-width: 958px) {
        grid-column: 1 / 1 span;
      }
    }

    @media (max-width: 1024px) {
      grid-column: 1 / 6 span;
    }
  }

  &__price {
    grid-column: 9 / span 4;

    @media (max-width: 1024px) {
      grid-column: 7 / 3 span;
    }

    @media (max-width: 958px) {
      grid-column: 1 / 1 span;
      grid-row: 2 / 1 span;
    }
  }

  @media (max-width: 958px) {
    grid-template-columns: 1fr;
    margin-bottom: 35px;
    row-gap: 30px;
  }
}
