.city {
  position: relative;
  padding-top: 114px;

  &__content {
    border-radius: 36px;
    padding-top: 78px;
    padding-bottom: 56px;
    background: url("../img/map.svg") right top / auto no-repeat, linear-gradient(129.12deg, #3d3d3d 0%, #303030 100%);
    row-gap: 70px;

    @media (max-width: 1024px) {
      background: linear-gradient(129.12deg, #3d3d3d 0%, #303030 100%);
    }

    @media (max-width: 600px) {
      padding-right: 30px;
      padding-left: 30px;
    }
  }

  &__title {
    grid-column: 2 / 6 span;
    font-weight: 600;
    font-size: 27px;
    line-height: 33px;

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__descr {
    grid-column: 2 / 5 span;
    grid-row: 2 / 1 span;
    font-weight: 400;

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__form {
    display: flex;
    align-items: flex-start;
    grid-column: 2 / 10 span;
    grid-row: 3 / 1 span;
    padding-top: 11px;

    label {
      &:not(:last-child) {
        margin-right: 8px;

        @media (max-width: 768px) {
          margin-right: 0;
        }
      }

      &:first-child {
        margin: 0;
      }
    }

    input {
      @media (max-width: 768px) {
        width: 100%;
      }
    }

    button {
      @media (max-width: 768px) {
        width: 100%;
      }
    }

    @media (max-width: 768px) {
      flex-flow: column wrap;
      justify-self: start;
      grid-column: 2 / 4 span;
      gap: 10px;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 50px;
  }
}
