.main-btn {
  border-radius: 8px;
  padding: 16px 32px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.2em;
  color: var(--light-color);
  background-color: var(--pink-color);
  transition: background-color 0.3s ease-in-out;

  &:hover {
    background-color: var(--boulder-color);
    transition: background-color 0.3s ease-in-out;
  }

  @media (max-width: 768px) {
    font-size: 14px;
  }
}
