.franch {
  &__image {
    margin-bottom: 34px;
    filter: grayscale(1);
    transition: filter 0.3s ease-in-out;
  }

  &__txt {
    font-size: 12px;
    color: var(--light-color);

    span {
      text-decoration: none;
      transition: color 0.3s ease-in-out;
    }
  }

  &:hover {
    &__image {
      filter: grayscale(0);
    }

    span {
      color: #f9b200;
      transition: color 0.3s ease-in-out;
    }
  }

  &:hover &__image {
    filter: grayscale(0);
    transition: filter 0.3s ease-in-out;
  }
}
