/* service */
.service {
  padding-top: 147px;

  &__top {
    display: grid;
    align-items: flex-start;
    justify-content: space-between;
    grid-template-columns: 2fr 1fr;
    margin-bottom: 51px;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      margin-bottom: 20px;
      row-gap: 50px;
    }
  }
}

/* service-slider */
.service-slider {
  width: 100%;

  &__nav {
    justify-self: end;
    transform: rotate(180deg);

    @media (max-width: 768px) {
      justify-self: start;
    }
  }

  &__wrapper {
    position: relative;
  }

  &__slide {
    grid-template-rows: 195px 70px auto 50px;

    @media (max-width: 1024px) {
      grid-template-rows: auto auto auto 50px;
    }

    @media (max-width: 768px) {
      grid-template-rows: auto auto auto auto 20px;
      row-gap: 20px;
    }
  }

  &__number {
    grid-column: 1 / 4 span;
    font-weight: 700;
    font-size: 64px;
    line-height: 1;
    text-transform: uppercase;
    color: var(--alto-color);

    @media (max-width: 1024px) {
      justify-self: start;
      padding-top: 0;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 3 span;
      grid-row: 2 / 1 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 6 span;
    }
  }

  &__title {
    grid-column: 1 / 4 span;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.21em;
    text-transform: uppercase;

    @media (max-width: 768px) {
      grid-column: 1 / 4 span;
      grid-row: 3 / 1 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 6 span;
    }
  }

  &__list {
    grid-column: 1 / 5 span;
    padding-left: 13px;

    @media (max-width: 768px) {
      grid-column: 1 / 6 span;
      grid-row: 4 / 1 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 6 span;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 23px;
    }
  }

  &__image-wrapper {
    grid-column: 6 / 7 span;
    grid-row: 1 / 4 span;
    border-radius: 36px;

    @media (max-width: 1024px) {
      grid-column: 5 / 5 span;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 5 span;
      grid-row: 1 / 1 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 6 span;
    }
  }

  &__image {
    border-radius: 36px;
  }

  &__pag {
    left: 0;
    right: auto;

    @media (max-width: 768px) {
      right: 0;
      justify-self: center;
    }
  }
}
