.section-title {
  position: relative;
  font-weight: 600;
  font-size: 36px;
  line-height: 1.2em;
  color: var(--dark-color);

  &::before {
    content: "";
    position: absolute;
    bottom: -8px;
    border-radius: 8px;
    width: 52px;
    height: 4px;
    background-color: var(--pink-color);

    @media (max-width: 1024px) {
      bottom: -3px;
    }

    @media (max-width: 768px) {
      left: 0;
      bottom: -1px;
      width: 35px;
    }
  }

  &--light {
    color: var(--light-color);
  }

  &--grid {
    &::before {
      bottom: 14px;
    }

    @media (max-width: 768px) {
      bottom: 0;
    }
  }

  @media (max-width: 468px) {
    font-size: 28px;
  }

  @media (max-width: 420px) {
    font-size: 28px;
  }
}

.section-descr {
  font-weight: 600;
  font-size: 16px;
  line-height: 1.2em;
  color: var(--gray-color);

  &--light {
    color: var(--light-color);
  }

  @media (max-width: 768px) {
    font-size: 14px;
  }
}

.text-uppercase {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
}

.white-space {
  white-space: nowrap;
}
