/* team */
.team {
  padding: 100px 0;
  background: linear-gradient(129.12deg, #3d3d3d 0%, #303030 100%);

  &__container {
    display: grid;
    align-items: start;
    grid-template-columns: 4fr 1fr;
    row-gap: 50px;

    @media (max-width: 568px) {
      grid-template-columns: 1fr;
    }
  }

  &__title {
    @media (max-width: 717px) {
      &::before {
        bottom: -8px;
      }
    }
  }

  &__nav {
    justify-self: end;

    @media (max-width: 568px) {
      justify-self: start;
      grid-row: 3 / 1 span;
    }
  }

  &__descr {
    font-weight: 400;
    color: var(--light-color);
  }

  &__content {
    padding-top: 63px;
    padding-left: max(15px, calc((100% - 1170px) / 2));

    @media (max-width: 568px) {
      padding-top: 20px;
    }
  }

  &__btn {
    border-color: var(--light-color);

    svg {
      fill: var(--light-color);
    }
  }
}

/* team-slider */
.team-slider {
  margin-bottom: 64px;
  color: var(--light-color);

  &__wrapper {
    display: flex;
  }

  &__slide {
    display: flex;
    flex-flow: column wrap;
    margin-right: 30px;
    width: 270px;
  }

  &__image {
    margin-bottom: 13px;
  }

  &__name {
    display: block;
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.21em;
  }

  &__post {
    display: block;
    font-size: 14px;
    line-height: 1.21em;
  }
}
