.you-get {
  padding-top: 155px;
  padding-bottom: 159px;
  background: url("../img/you-get-bg.svg") 0 609px / auto no-repeat;

  &__container {
    align-items: center;
    grid-template-rows: 230px auto;
    row-gap: 18px;

    @media (max-width: 850px) {
      grid-template-columns: 1fr;
      grid-template-rows: repeat(3, auto);
      row-gap: 40px;
    }
  }

  &__title {
    align-self: start;
    grid-column: 1 / 5 span;
    grid-row: 1 / 1 span;

    @media (max-width: 1024px) {
      grid-column: 1 / 4 span;
    }

    @media (max-width: 850px) {
      grid-column: 1 / 9 span;
      grid-row: 1 / 1 span;
    }
  }

  &__descr {
    display: grid;
    align-self: start;
    grid-column: 1 / 5 span;
    grid-row: 2 / 1 span;
    padding-top: 18px;
    line-height: 1.21em;
    color: var(--dark-color);
    row-gap: 22px;

    @media (max-width: 1024px) {
      grid-column: 1 / 4 span;
    }

    @media (max-width: 850px) {
      grid-column: 1 / 9 span;
      grid-row: 2 / 1 span;
    }
  }

  &__list {
    display: grid;
    justify-content: center;
    grid-column: 7 / 6 span;
    grid-row: 1 / 2 span;
    border-radius: 36px;
    padding-top: 86px;
    padding-bottom: 81px;
    background: url("../img/we-do-list-bg.svg") left bottom / auto no-repeat, linear-gradient(129.12deg, #3d3d3d 0%, #303030 100%);
    row-gap: 48px;

    @media (max-width: 1024px) {
      grid-column: 6 / 10 span;
    }

    @media (max-width: 850px) {
      grid-column: 1 / 9 span;
      grid-row: 3 / 1 span;
      padding: 80px 30px;
    }
  }

  &__item {
    position: relative;
    padding-left: 100px;
    width: 375px;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.2em;
    text-transform: uppercase;
    color: var(--light-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      border-radius: 16px;
      width: 70px;
      height: 70px;
      background-image: url("../img/we-do-item-1.svg");
      background-position: center;
      background-size: auto;
      background-repeat: no-repeat;
      background-color: var(--light-color);

      @media (max-width: 1024px) {
        width: 50px;
        height: 50px;
        background-size: 30px 30px;
      }
    }

    @media (max-width: 1024px) {
      padding-left: 60px;
    }

    @media (max-width: 850px) {
      width: 100%;
    }
  }

  @media (max-width: 850px) {
    padding-top: 100px;
  }
}

// you-get-item
.you-get-item {
  &__title {
    margin-bottom: 16px;
    font-weight: 500;
  }

  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.21em;
    text-transform: none;

    &:not(:last-child) {
      margin-bottom: 17px;
    }
  }
}
