/* option */
.option {
  padding-top: 143px;

  &__container {
    grid-template-rows: repeat(3, auto);
  }

  &__title {
    margin-bottom: 50px;
    width: 70%;
  }

  &__descr {
    margin-bottom: 50px;
  }

  &__list {
    column-count: 2;

    @media (max-width: 1024px) {
      column-count: 1;
    }
  }
}

/* option-item */
.option-item {
  position: relative;
  margin-bottom: 50px;
  font-weight: 700;
  font-size: 26px;
  line-height: 1;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 24px;
    height: 100%;
    background: url("../img/check-circle.svg") left top / auto no-repeat;
  }

  &__descr {
    margin-bottom: 26px;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.21em;

    &:first-child {
      margin-top: 22px;

      @media (max-width: 600px) {
        margin-top: 15px;
      }
    }

    @media (max-width: 600px) {
      font-size: 12px;
    }
  }

  @media (max-width: 600px) {
    font-size: 20px;
  }
}

/* option-counter */
.option-counter {
  counter-reset: option-counter;

  &__item {
    counter-increment: option-counter;
    padding-left: 50px;

    &::before {
      content: ""counter(option-counter) ".";
      color: var(--dark-color);
    }
  }
}
