/* modal */
.modal {
  &__container {
    border: 1px solid #4d8230;
    border-radius: 8px;
    padding: 96px 30px 64px;
    max-width: 430px;
    box-shadow: 0 100px 203px rgb(0 0 0 / 3%), 0 36.5016px 74.0983px rgb(0 0 0 / 2.07%), 0 17.7209px 35.9734px rgb(0 0 0 / 1.67%), 0 8.6871px 17.6348px rgb(0 0 0 / 1.33%), 0 3.43489px 6.97283px rgb(0 0 0 / 0.93%);
    background: var(--light-color);
  }

  &__content {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
  }

  &__title {
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 24px;
    text-align: center;

    &::before {
      display: none;
    }
  }

  &__descr {
    margin-bottom: 36px;
    font-weight: 400;
    text-align: center;
  }

  &__form {
    display: flex;
    flex-flow: column wrap;

    input {
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 2px;
      }
    }
  }
}

/* modal-form */
.modal-form {
  max-width: 304px;

  &__label {
    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &:last-child {
      margin-bottom: 36px;
    }
  }
}

/* modal-close */
.modal-close {
  right: 36px;
  top: 36px;

  &__svg {
    width: 12px;
    height: 12px;
    stroke: var(--gray-color);
    stroke-width: 2px;
    transition: stroke 0.3s ease-in-out;

    &:hover {
      stroke: var(--pink-color);
      transition: stroke 0.3s ease-in-out;
    }
  }
}
