.example {
  padding-top: 23px;
  padding-bottom: 50px;
  background: url("../img/roller.svg") right 65px / auto no-repeat;

  &__title {
    margin-bottom: 87px;
  }

  &__list {
    column-gap: 30px;
    row-gap: 80px;

    @media (max-width: 1024px) {
      column-gap: 15px;
      row-gap: 80px;
    }

    @media (max-width: 653px) {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    position: relative;
    min-height: 304px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

    &:nth-child(1) {
      grid-column: 6 / 7 span;
      grid-row: 1 / 2 span;
      background-image: url("../img/example-item-1.jpg");

      @media (max-width: 1024px) {
        grid-column: 5 / 10 span;
      }

      @media (max-width: 653px) {
        grid-column: 1 / 1 span;
        grid-row: 1 / 1 span;
      }
    }

    &:nth-child(2) {
      grid-column: 1 / 5 span;
      background-image: url("../img/example-item-2.jpg");

      @media (max-width: 1024px) {
        grid-column: 1 / 4 span;
      }

      @media (max-width: 653px) {
        grid-column: 1 / 1 span;
        grid-row: 2 / 1 span;
      }
    }

    &:nth-child(3) {
      grid-column: 1 / 5 span;
      grid-row: 2 / 1 span;
      background-image: url("../img/example-item-3.jpg");

      @media (max-width: 1024px) {
        grid-column: 1 / 4 span;
      }

      @media (max-width: 653px) {
        grid-column: 1 / 1 span;
        grid-row: 3 / 1 span;
      }
    }

    @media (max-width: 768px) {
      min-height: 260px;
    }

    @media (max-width: 468px) {
      height: 360px;
    }
  }
}

// example-item

// example-item-descr
.example-item-descr {
  position: absolute;
  right: 0;
  bottom: -40px;
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 470px;
  font-size: 18px;
  line-height: 1.21em;

  &__name {
    display: block;
    justify-self: end;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.21em;
  }

  &__price {
    display: block;
    justify-self: end;
  }

  &__text {
    color: var(--boulder-color);
  }

  &__numbers {
    font-weight: 700;
    color: var(--pink-color);
  }

  @media (max-width: 1024px) {
    bottom: -50px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
  }
}
