/* base-tariff */
.base-tariff {
  padding-top: 158px;
  background: url("../img/base-tariff-bg-cube.svg") 96% 29% / auto no-repeat;

  &__top {
    margin-bottom: 95px;

    @media (max-width: 958px) {
      grid-template-columns: 1fr;
      margin-bottom: 35px;
      row-gap: 30px;
    }
  }

  &__title {
    position: relative;
    grid-column: 1 / 8 span;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: 34%;
      width: 27.5%;
      height: 1px;
      background: url("../img/bg-line.svg") right center / cover no-repeat;

      @media (max-width: 1024px) {
        display: none;
      }

      @media (max-width: 958px) {
        grid-column: 1 / 1 span;
      }
    }

    @media (max-width: 1070px) {
      &::before {
        bottom: -8px;
      }
    }

    @media (max-width: 1024px) {
      grid-column: 1 / 6 span;
    }
  }

  &__price {
    grid-column: 9 / span 4;

    @media (max-width: 1024px) {
      grid-column: 7 / 3 span;
    }

    @media (max-width: 958px) {
      grid-column: 1 / 1 span;
      grid-row: 2 / 1 span;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 70px;
  }
}

/* base-tariff-list */
.base-tariff-list {
  display: grid;
  column-gap: 30px;
  grid-template-columns: repeat(3, 1fr);

  &__item {
    border-radius: 36px;
    padding: 0 48px;
    padding-top: 46px;
    min-height: 733px;
    box-shadow: 0 80px 155px rgb(0 0 0 / 7%), 0 29.2013px 56.5775px rgb(0 0 0 / 4.83%), 0 14.1767px 27.4674px rgb(0 0 0 / 3.89%), 0 6.94968px 13.465px rgb(0 0 0 / 3.11%), 0 2.74791px 5.32408px rgb(0 0 0 / 2.17%);
    background-color: var(--light-color);

    @media (max-width: 1024px) {
      padding-right: 30px;
      padding-left: 30px;
      min-height: 620px;
    }

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      padding-bottom: 50px;
      min-height: auto;
    }
  }

  &__title {
    position: relative;
    margin-bottom: 66px;
    padding-top: 78px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.21em;
    text-transform: uppercase;
    background-position: left top;
    background-size: auto;
    background-repeat: no-repeat;

    &::before {
      content: "";
      position: absolute;
      bottom: -16px;
      border-radius: 8px;
      width: 100%;
      height: 2px;
      background-color: var(--pink-color);
    }

    &--1 {
      background-image: url("../img/base-tariff-item-2.svg");
    }

    &--2 {
      background-image: url("../img/base-tariff-item-1.svg");
    }

    &--3 {
      background-image: url("../img/base-tariff-item-3.svg");
    }
  }

  @media (max-width: 958px) {
    column-gap: 10px;
  }

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    row-gap: 30px;
  }
}

/* base-tariff-second-list */
.base-tariff-second-list {
  display: grid;
  grid-template-columns: 1fr;
  padding-right: 2px;
  padding-left: 12px;
  line-height: 1.21em;
  row-gap: 22px;

  @media (max-width: 1024px) {
    padding: 0;
    font-size: 14px;
  }
}
