/* ty-modal */
.ty-modal {
  &__content {
    position: relative;
    display: none;
    flex-flow: column wrap;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

    &--visible {
      display: flex;
      opacity: 1;
      visibility: visible;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }
  }

  &__list {
    display: grid;
    column-gap: 32px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__svg {
    width: 64px;
    height: 64px;
    fill: var(--pink-color);
    transition: fill 0.3s ease-in-out;

    &:hover {
      fill: var(--light-gray);
      transition: fill 0.3s ease-in-out;
    }
  }
}
