.pres {
  padding-top: 172px;

  &__content {
    position: relative;
    border-radius: 36px;
    padding-top: 93px;
    padding-bottom: 70px;
    background: url("../img/pres-bg.svg") 100% 100% / auto no-repeat, linear-gradient(129.12deg, #494949 0%, #303030 100%);

    @media (max-width: 600px) {
      padding: 70px 15px;
      background: linear-gradient(129.12deg, #494949 0%, #303030 100%);
    }
  }

  &__title {
    grid-column: 2 / 5 span;
    margin-bottom: 48px;
    font-size: 27px;

    @media (max-width: 1024px) {
      grid-column: 2 / 4 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__descr {
    grid-column: 2 / 4 span;
    grid-row: 2 / 1 span;
    margin-bottom: 36px;
    font-weight: 400;

    @media (max-width: 1024px) {
      grid-column: 2 / 4 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__btn-wrapper {
    grid-column: 2 / 2 span;
    grid-row: 3 / 1 span;

    @media (max-width: 1024px) {
      grid-column: 2 / 3 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 3 span;
    }
  }

  &__image {
    position: absolute;
    right: 74px;
    top: -21%;

    @media (max-width: 1130px) {
      right: 0;
    }

    @media (max-width: 1024px) {
      display: none;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 100px;
    padding-bottom: 50px;
  }
}
