.how {
  padding-bottom: 100px;

  &__content {
    position: relative;
    border-radius: 36px;
    padding-top: 84px;
    padding-bottom: 64px;
    background: linear-gradient(129.12deg, #494949 0%, #303030 100%);
    row-gap: 50px;

    @media (max-width: 1025px) {
      padding-bottom: 0;
    }

    @media (max-width: 600px) {
      grid-template-columns: 1fr;
      padding: 60px 20px;
    }
  }

  &__title {
    grid-column: 2 / 4 span;
    font-size: 27px;

    @media (max-width: 1024px) {
      grid-column: 2 / 7 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 1 span;
    }
  }

  &__img-wrapper {
    position: absolute;
    right: -30px;
    top: 54px;

    @media (max-width: 1288px) {
      right: 10px;
    }

    @media (max-width: 1024px) {
      position: static;
      display: block;
      grid-column: 2 / 7 span;
      grid-row: 4 / 1 span;
      margin-bottom: -80px;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 1 span;
    }
  }

  &__img {
    border-radius: 36px;
  }

  &__descr {
    grid-column: 2 / 5 span;
    grid-row: 2 / 1 span;
    font-weight: 400;
    color: var(--light-color);

    span {
      display: block;
      margin-bottom: 25px;
      width: 90%;
    }

    @media (max-width: 1024px) {
      grid-column: 2 / 8 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 1 span;
    }
  }

  &__btn-wrapper {
    grid-column: 2 / 2 span;
    grid-row: 3 / 1 span;

    @media (max-width: 600px) {
      grid-column: 1 / 1 span;
    }
  }
}

.how-descr {
  &__item {
    &::before {
      content: "—";
    }
  }
}
