.income {
  &__big-text {
    display: block;
    margin-bottom: 9px;
    font-weight: 700;
    font-size: 64px;
    line-height: 1.21em;
    text-transform: uppercase;
    color: var(--pink-color);

    @media (max-width: 1070px) {
      font-size: 50px;
    }

    @media (max-width: 468px) {
      font-size: 40px;
    }
  }
}
