.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 1000;
  padding: 43px 0;

  &__logo {
    display: flex;
    align-items: center;
    grid-column: 1 / 7;

    img {
      margin-right: 100px;

      @media (max-width: 1196px) {
        margin-right: 30px;
      }
    }

    span {
      font-weight: 400;
      font-size: 14px;
      line-height: 1.2em;
      color: var(--light-gray);
    }

    @media (max-width: 1024px) {
      grid-column: 1 / 7;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 4;

      span {
        display: none;
      }
    }
  }

  &__contacts {
    display: flex;
    align-items: center;
    grid-column: 8 / 13;
    list-style: none;
    margin-left: 40px;

    @media (max-width: 1196px) {
      margin-left: 0;
    }

    @media (max-width: 1024px) {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      display: flex;
      flex-flow: column wrap;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100vh;
      background-color: var(--dark-color);
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 64px;
    }

    @media (max-width: 1100px) {
      &:not(:last-child) {
        margin-right: 30px;
      }
    }

    @media (max-width: 1024px) {
      &:not(:last-child) {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.2;
    color: var(--light-color);
    transition: color 0.3s ease-in-out;

    svg {
      margin-right: 16px;
      width: 16px;
      height: 16px;
      fill: transparent;
      stroke: var(--light-color);
      stroke-width: 2px;
      transition: stroke 0.3s ease-in-out;
    }

    &:hover {
      color: var(--pink-color);
      transition: color 0.3s ease-in-out;

      svg {
        stroke: var(--pink-color);
        transition: stroke 0.3s ease-in-out;
      }
    }

    @media (max-width: 1024px) {
      font-size: 20px;

      svg {
        width: 20px;
        height: 20px;
      }
    }

    @media (max-width: 340px) {
      font-size: 15px;

      svg {
        width: 15px;
        height: 15px;
      }
    }
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
