/* feedback */
.feedback {
  padding-top: 154px;

  &__container {
    display: grid;
    align-items: start;
    grid-template-columns: 1fr 1fr;

    @media (max-width: 568px) {
      grid-template-columns: 1fr;
      row-gap: 20px;
    }
  }

  &__nav {
    justify-self: end;
  }

  &__content {
    display: grid;
    column-gap: 110px;
    grid-template-columns: 470px auto;
    grid-template-rows: 165px auto;
    padding-left: max(15px, calc((100% - 1170px) / 2));

    @media (max-width: 1024px) {
      column-gap: 0;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: auto;
      row-gap: 20px;
    }

    @media (max-width: 569px) {
      padding: 0;
    }
  }

  &__subtitle {
    grid-column: 1 / 1 span;
    grid-row: 1 / 1 span;
    padding-top: 50px;

    @media (max-width: 1024px) {
      grid-column: 1 / 4 span;
      grid-row: 2 / 1 span;
      padding: 0;
    }

    @media (max-width: 569px) {
      padding: 0 15px;
    }
  }

  &__descr {
    grid-column: 1 / 1 span;

    @media (max-width: 1024px) {
      grid-column: 1 / 4 span;
      grid-row: 3 / 1 span;
      font-size: 14px;
    }

    @media (max-width: 569px) {
      padding: 0 15px;
    }
  }
}

/* feedback-descr */
.feedback-descr {
  &__items:not(:last-child) {
    margin-bottom: 25px;
  }
}

/* feedback-slider */
.feedback-slider {
  grid-column: 2 / 1 span;
  grid-row: 1 / 2 span;
  padding-bottom: 50px;
  padding-left: 20px;
  width: 100vw;

  &__slide {
    width: 571px;
    filter: drop-shadow(0 8px 10px rgb(0 0 0 / 7%)) drop-shadow(0 6px 11px rgb(0 0 0 / 4.83%)) drop-shadow(0 3px 5px rgb(0 0 0 / 3.89%)) drop-shadow(0 1.2px 2.5px rgb(0 0 0 / 3.11%)) drop-shadow(0 0.4px 1px rgb(0 0 0 / 2.17%));

    @media (max-width: 768px) {
      width: 300px;
    }

    @media (max-width: 468px) {
      width: 320px;
    }
  }

  &__image {
    border-radius: 36px;
    object-fit: cover;
  }

  @media (max-width: 1024px) {
    grid-column: 1 / 4 span;
    grid-row: 1 / 1 span;
    padding: 50px 0;
    padding-left: 10px;
  }

  @media (max-width: 569px) {
    padding-left: 0;
  }
}
