.result {
  padding-top: 157px;
  padding-bottom: 130px;

  &__container {
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__title {
    grid-column: 1 / 4 span;

    @media (max-width: 1107px) {
      &::before {
        bottom: -8px;
      }
    }

    @media (max-width: 768px) {
      grid-column: 1 / 1 span;
      margin-bottom: 20px;
    }
  }

  &__line {
    display: block;
    align-self: center;
    grid-column: 5 / 4 span;
    margin-bottom: 30px;
    height: 1px;
    background: url("../img/bg-line.svg") center / cover no-repeat;

    @media (max-width: 1024px) {
      grid-column: 5 / 1 span;
    }

    @media (max-width: 795px) {
      display: none;
    }
  }

  &__income {
    grid-column: 9 / 4 span;

    @media (max-width: 1024px) {
      grid-column: 6 / 4 span;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 1 span;
      grid-row: 2 / 1 span;
    }
  }

  &__descr {
    grid-column: 1 / 4 span;
    grid-row: 2 / 1 span;
    padding-top: 32px;

    @media (max-width: 1024px) {
      grid-column: 1 / 5 span;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 1 span;
      grid-row: 3 / 1 span;
    }
  }

  &__list {
    grid-column: 1 / 12 span;
    grid-row: 3 / 1 span;
    padding-top: 67px;

    @media (max-width: 1024px) {
      grid-column: 1 / 9 span;
    }

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      padding-top: 100px;
      row-gap: 30px;
    }
  }

  &__item {
    &:nth-child(1) {
      grid-column: 1 / span 4;

      @media (max-width: 1024px) {
        grid-column: 1 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1 span;
      }
    }

    &:nth-child(2) {
      grid-column: 5 / span 4;

      @media (max-width: 1024px) {
        grid-column: 4 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1 span;
        grid-row: 2 / 1 span;
      }
    }

    &:nth-child(3) {
      grid-column: 9 / span 4;

      @media (max-width: 1024px) {
        grid-column: 7 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1 span;
        grid-row: 3 / 1 span;
      }
    }
  }
}

.result-item {
  &__big-text {
    display: block;
    margin-bottom: 17px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.21em;
    text-transform: uppercase;
  }

  &__small-text {
    line-height: 1.21em;
    color: var(--gray-color);
  }
}
