.royalty-benefits {
  padding-top: 158px;

  &__top {
    margin-bottom: 90px;

    @media (max-width: 768px) {
      margin-bottom: 40px;
    }
  }

  &__title {
    &::after {
      width: 54.5%;

      @media (max-width: 1192px) {
        width: 40%;
      }
    }

    @media (max-width: 1070px) {
      &::before {
        bottom: -8px;
      }
    }
  }

  &__list {
    column-count: 3;
    column-gap: 65px;

    @media (max-width: 768px) {
      column-count: 2;
    }

    @media (max-width: 568px) {
      column-count: 1;
    }
  }

  &__item {
    padding-left: 30px;

    &:not(:last-child) {
      margin-bottom: 22px;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 70px;
  }
}
