.form {
  &__input {
    border: 1px solid var(--boulder-color);
    border-radius: 8px;
    padding-left: 32px;
    width: 200px;
    height: 52px;
    font-size: 16px;
    line-height: 1.2em;
    color: var(--boulder-color);
    background: transparent !important;

    &--modal {
      width: 100%;
    }
  }
}
