/* we-do */
.we-do {
  padding-top: 148px;

  &__container {
    align-items: center;
    grid-template-rows: 195px auto;
    row-gap: 18px;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      grid-template-rows: repeat(3, auto);
    }
  }

  &__title {
    align-self: start;
    grid-column: 1 / 6 span;
    grid-row: 1 / 1 span;
    padding-top: 38px;
    width: 81%;

    @media (max-width: 1024px) {
      grid-column: 1 / 4 span;
    }
  }

  &__descr {
    display: grid;
    align-self: start;
    grid-column: 1 / 5 span;
    grid-row: 2 / 1 span;
    line-height: 1.21em;
    color: var(--dark-color);
    row-gap: 22px;

    @media (max-width: 1024px) {
      grid-column: 1 / 4 span;
    }
  }

  &__list {
    display: grid;
    justify-content: center;
    grid-column: 7 / 6 span;
    grid-row: 1 / 2 span;
    border-radius: 36px;
    padding-top: 86px;
    padding-bottom: 87px;
    background: url("../img/we-do-list-bg.svg") left bottom / auto no-repeat, linear-gradient(129.12deg, #3d3d3d 0%, #303030 100%);
    row-gap: 49px;

    @media (max-width: 1024px) {
      grid-column: 5 / 10 span;
    }

    @media (max-width: 768px) {
      grid-column: 1 / 1 span;
      grid-row: 3 / 1 span;
    }

    @media (max-width: 468px) {
      padding: 60px 10px;
    }
  }

  &__item {
    position: relative;
    padding-left: 100px;
    width: 400px;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.2em;
    text-transform: uppercase;
    color: var(--light-color);

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      border-radius: 16px;
      width: 70px;
      height: 70px;
      background-position: center;
      background-size: auto;
      background-repeat: no-repeat;
      background-color: var(--light-color);
      transform: translateY(-50%);
    }

    &:nth-child(1) {
      &::before {
        background-image: url("../img/we-do-item-1.svg");
      }
    }

    &:nth-child(2) {
      &::before {
        background-image: url("../img/we-do-item-2.svg");
      }
    }

    &:nth-child(3) {
      &::before {
        background-image: url("../img/we-do-item-3.svg");
      }
    }

    &:nth-child(4) {
      &::before {
        background-image: url("../img/we-do-item-4.svg");
      }
    }

    @media (max-width: 468px) {
      padding-left: 60px;
      max-width: 270px;
      font-size: 14px;

      &::before {
        width: 50px;
        height: 50px;
        background-size: 30px 30px;
      }
    }
  }

  @media (max-width: 768px) {
    padding-top: 30px;
  }
}
