.footer {
  margin-top: -104px;
  padding-top: 238px;
  padding-bottom: 70px;
  background-color: #202020;

  &__logo {
    grid-column: 1 / 3 span;

    @media (max-width: 600px) {
      grid-column: 1 / 4 span;
    }
  }

  &__container {
    row-gap: 30px;
  }

  &__contacts {
    align-self: start;
    grid-column: 4 / 3 span;
    font-size: 12px;
    line-height: 1.5em;
    color: var(--light-color);

    @media (max-width: 600px) {
      grid-column: 1 / 3 span;
      grid-row: 2 / 1 span;
    }
  }

  &__social-media {
    grid-column: 8 / 2 span;

    @media (max-width: 1024px) {
      grid-column: 1 / 2 span;
      grid-row: 2 / 1 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 2 span;
      grid-row: 3 / 1 span;
    }
  }

  &__franch {
    grid-column: 11 / 2 span;

    @media (max-width: 1024px) {
      grid-column: 4 / 2 span;
      grid-row: 2 / 1 span;
    }

    @media (max-width: 600px) {
      grid-column: 1 / 3 span;
      grid-row: 4 / 1 span;
    }
  }

  @media (max-width: 1024px) {
    padding-top: 180px;
  }
}

/* footer-social-media */
.footer-social-media {
  display: grid;
  column-gap: 16px;
  justify-content: start;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: repeat(2, auto);
  row-gap: 6px;

  &__item {
    width: 32px;
    height: 32px;
  }

  &__link {
    display: block;
  }

  &__svg {
    width: 32px;
    height: 32px;
    fill: var(--light-color);
    transition: fill 0.3s ease-in-out;

    &:hover {
      fill: var(--pink-color);
      transition: fill 0.3s ease-in-out;
    }
  }
}
