// reset default settings
.swiper-button-prev,
.swiper-button-next {
  position: static;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.slider-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  grid-template-columns: repeat(2, 1fr);
  width: 88px;

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    border: 2px solid var(--napa-color);
    border-radius: 100%;
    width: 36px;
    height: 36px;
    transition: border-color 0.3s ease-in-out;
    cursor: pointer;

    svg {
      display: block;
      width: 9px;
      height: 14.4px;
      fill: var(--napa-color);
      transition: fill 0.3s ease-in-out;
    }

    &:hover {
      border-color: var(--pink-color);
      transition: border-color 0.3s ease-in-out;

      svg {
        fill: var(--pink-color);
        transition: fill 0.3s ease-in-out;
      }
    }

    &--next {
      transform: rotate(180deg);
    }
  }
}
