.hero {
  padding-top: 283px;
  padding-bottom: 47px;
  background: url("../img/hero-bg.png") center 70% / cover no-repeat, var(--dark-color);
  background-blend-mode: overlay;

  &__container {
    row-gap: 120px;

    @media (max-width: 768px) {
      row-gap: 50px;
    }
  }

  &__top {
    grid-column: 1 / 10;
    grid-row: 1 / 1;
    padding-left: 6px;

    @media (max-width: 1024px) {
      grid-column: 1 / 8;
    }
  }

  &__title {
    margin-bottom: 47px;
    font-weight: 700;
    font-size: 48px;

    @media (max-width: 768px) {
      margin-bottom: 35px;
      font-size: 30px;
    }

    @media (max-width: 468px) {
      font-size: 25px;
    }
  }

  &__descr {
    z-index: 0;
    margin-bottom: 64px;
    width: 60%;
    font-size: 22px;
    color: var(--light-color);

    @media (max-width: 768px) {
      margin-bottom: 35px;
      width: 100%;
      font-size: 14px;
    }

    @media (max-width: 468px) {
      font-size: 14px;
    }
  }

  &__list {
    display: grid;
    column-gap: 30px;
    grid-column: 1 / 13;
    grid-row: 2 / 2;
    grid-template-columns: repeat(12, 1fr);

    @media (max-width: 1024px) {
      grid-column: 1 / 9;
      grid-template-columns: repeat(9, 1fr);
    }

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      grid-template-rows: (3, 1fr);
      row-gap: 40px;
    }
  }

  &__item {
    padding-top: 80px;
    width: 79%;
    font-weight: 400;
    font-size: 16px;
    color: var(--mercury-color);
    background-position: left top;
    background-size: 3em;
    background-repeat: no-repeat;

    &:nth-child(1) {
      grid-column: 1 / 5;
      background-image: url("../img/hero-item-1.svg");

      @media (max-width: 1024px) {
        grid-column: 1 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1;
        grid-row: 1 / 1;
      }
    }

    &:nth-child(2) {
      grid-column: 5 / 9;
      background-image: url("../img/hero-item-2.svg");

      @media (max-width: 1024px) {
        grid-column: 4 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1;
        grid-row: 2 / 2;
      }
    }

    &:nth-child(3) {
      grid-column: 9 / 13;
      background-image: url("../img/hero-item-3.svg");

      @media (max-width: 1024px) {
        grid-column: 7 / 3 span;
      }

      @media (max-width: 768px) {
        grid-column: 1 / 1;
        grid-row: 3 / 3;
      }
    }

    @media (max-width: 1024px) {
      width: 100%;
    }

    @media (max-width: 768px) {
      padding-top: 60px;
      font-size: 14px;
    }
  }

  @media (max-width: 1024px) {
    background-size: cover;
  }

  @media (max-width: 768px) {
    padding-top: 150px;
    padding-bottom: 50px;
  }
}
